          name: "Сорвиголова ",
          nameor: "Daredevil",
          description:
            'Брошенный матерью, Мэтт Мердок был воспитан своим отцом, боксером "Сражающимся Джеком" Мердоком, в Адской кухне. Понимая, что правила необходимы для того, чтобы люди не вели себя плохо, молодой Мэтт решил изучать право; однако, когда он спас человека от встречного грузовика, он разлил радиоактивный груз, который сделал Мэтта слепым, одновременно усиливая его оставшиеся чувства. Под суровой опекой слепого мастера боевых искусств Стика Мэтт овладел своими обостренными чувствами и стал грозным бойцом.',
