  methods: {
    fetchCharacters: function() {
      return fetch(`https://netology-api-marvel.herokuapp.com/characters`)
        .then((response) => response.json())
        .then((json) => (this.characters = json));
    },
    changeSearch: function(value) {
      this.search = value;
    },
  },
  computed: {
    character: function() {
      return this.searchCharacters[this.characterIndex] || null;
    },
    searchCharacters: function() {
      const { characters, search } = this;
      return characters.filter((character) => {
        return (
          character.name.toLowerCase().indexOf(search.toLowerCase()) !== -1
        );
      });
    },
  },
  async mounted() {
    this.loading = true;
    //await this.fetchCharacters();// this string  - to get characters form api; characters are in the data now, how to get them locally form characters.json - second question
    this.loading = false;
  },
};
